import React from "react";
import MyPosts from "./MyPosts/MyPosts";
import Post from "./../Profile/MyPosts/Post/Post";
import s from "./Profile.module.css";

const Profile = () => {
  return (
    <div>
      <div>
        <img
          className={s.imgheader}
          src="https://www.beststartcompton.org/wp-content/uploads/2019/02/39875853-header-wallpapers.jpg"
          alt="content-header"
        />
      </div>
      <MyPosts />
      <Post message="Hello!" likesCount="3" />
      <Post message="props test" likesCount="5" />
      <Post message="I need a portfolio" likesCount="500" />
    </div>
  );
};
export default Profile;
