import React from "react";
import s from "./Post.module.css";

const Post = props => {
  return (
    <div className={s.post}>
      <img
        className={s.avatar}
        src="https://image.shutterstock.com/image-vector/accurate-silhouette-man-colored-triangles-600w-633229784.jpg"
        alt="avatar"
      />
      <div className={s.message}>{props.message}</div>
      <button>like</button>
      <div className={s.likesCount}>likes: {props.likesCount}</div>
    </div>
  );
};
export default Post;
